import React from 'react';
import { EMAIL, WEBSITE } from '../../utils/exportContent';

function PrivacyPolicy() {
    return (
        <div style={{paddingBottom: "50px"}}>
            <h1 style={{marginBottom: "40px"}}>PRIVACY POLICY</h1>
            <p style={{marginBottom: "20px"}}>VERSION 1.0, March 01, 2021</p>
            <div>
                <p>At Bharg Technologies GmbH ("we"/"our"/"us"), data protection and confidentiality is a high priority.</p>
                <p>This Privacy Policy explains what and how we process your personal data when you use our website, <a rel="noopener noreferrer" href={WEBSITE} target="_blank">www.bharg.com</a>. The website is operated by Bharg Technologies GmbH and is the data controller (i.e., the company that decides the means and purpose for processing your personal data).</p>
                <p>Contact information of data controller: Bharg Technologies GmbH, Pf√§ffikonerstrasse 27, 8834 Schindellegi, Switzerland. You can contact us at <a href={`mailto:${EMAIL}`}>hello@bharg.com</a>. Swiss commercial registration no (UID) CHE - 438.324.163.</p>
            </div>
            <div>
                <p style={text}>1. HOW WE USE YOUR PERSONAL DATA</p>
                <p>YOUR INQUIRIES</p>
                <p>If you contact us about our services, the email(s) you send / contact form submitted may include information about you, such as your name, address, age, gender, email address, and your inquiry. We use this information to carry out the following:</p>
                <ul style={{marginTop: '10px', marginLeft: "20px"}}>
                    <li>In line with the legitimate interest we have in promoting our business, we will process your inquiries to provide you with information about the services we offer.</li>
                    <li>This might include replying to your inquiry, or sending you invites to our events.</li>
                    <li>We use MailChimp and Google Gmail to process your email address and information supplied by you in the course of an inquiry through the website and to send you invitations and marketing materials. You can find out more about how they process personal data here: <a rel="noopener noreferrer" target="_blank" href="https://policies.google.com/privacy">https://policies.google.com/privacy</a> and <a rel="noopener noreferrer" target="_blank" href="https://mailchimp.com/legal/privacy/.">https://mailchimp.com/legal/privacy/</a>.</li>
                    <li>We may also process inquiries to take steps you ask of us to enter into an agreement to provide you with our services.</li>
                    <li>You are under no obligation to provide us with any details, but if you don‚Äôt provide all relevant information, we may not be able to help.</li>
                </ul>
                
                <p style={text}>MARKETING</p>
                <p>We might use your information to send you marketing emails, according to our legitimate interests, about our services or events that are similar or related to those you have previously received (or attended, in the case of events). We may also disclose your name and contact information to our relevant affiliates who provide the same services, to enable them to market their services and/or events to you. If you would like to be removed from this mailing list, please contact us at <a href={`mailto:${EMAIL}`}>hello@bharg.com</a> or click unsubscribe at the bottom of each marketing email.</p>

                <p style={text}>RECRUITMENT</p>
                <p>When you apply for a job on this site, the personal data contained in your application will be collected by us. Your personal data was either provided by yourself or obtained from publicly available sources (e.g. LinkedIn) or provided to us by someone who referred you for potential employment.</p>
                <p>Your personal data will be processed to manage our recruitment related activities in line with our legitimate interests, which include evaluating your candidacy/application for employment with us, setting up and conducting interviews and test for applicants, evaluating and assessing the results thereto, and as in otherwise needed in the recruitment and hiring processes.</p>
                <p>We will retain your personal application data until you request them to be removed. The data is used to improve our recruitment efforts and selection procedure and as a talent pool. Whenever we have a new opening, we can easily access the data of candidates who have shown interest in our company in the past and contact them to see if they are available for a new opportunity. If you have any objections to us retaining your information for these purposes, please contact <a rel="noopener noreferrer" href={WEBSITE} target="_blank">www.bharg.com</a>.</p>

                <p style={text}>ALL CONTENT YOU SUBMIT TO US</p>
                <p>If you send us objectionable content or otherwise behave in a disruptive manner when using our website, we may process personal data included in your messages to respond to and stop such behavior.</p>
                <ul style={{marginTop: '10px', marginLeft: "20px"}}>
                    <li>We only process personal data in this way for the legitimate interests of ensuring that the use of our website is lawful, does not disrupt, does not harass our staff or other individuals, and to enforce our legal rights and to comply with our legal obligations.</li>
                    <li>Where we reasonably believe you are or may be in breach of the law (for instance, because content you send amounts to harassment or is defamatory), we may use your personal information to inform relevant third parties such as your email/internet provider or law enforcement agencies about the content.</li>
                    <li>Where we process personal data in this way, we will hold that personal information on our systems for as long as is reasonably necessary to achieve these objectives.</li>
                </ul>

                <p style={text}>HOW YOU USE OUR WEBSITE (ANALYTICS)</p>
                <p>Our website uses cookies and other mechanisms to collect log and analytical information, such as your internet protocol (IP) address, to help analyze how visitors use the site and to compile statistical reports on website activity.</p>
                <ul style={{marginTop: '10px', marginLeft: "20px"}}>
                    <li>We process this information to understand how visitors use our website and to compile statistical reports regarding that activity (for example your IP address is used to approximate the country from which you access our website, and we aggregate this information together, so we know that, for example, most of the visitors to our website come from a certain country/area).</li>
                    <li>This processing is necessary for us to pursue our legitimate interests in improving our website by enhancing the user experience and making the website as personal and relevant as possible.</li>
                    <li>This information is not used to develop a personal profile of you. All such information is aggregated and stored anonymously.</li>
                </ul>

                <p style={text}>2. DO WE SHARE YOUR PERSONAL INFORMATION WITH ANYONE ELSE?</p>
                <p>Your personal data on our website is used by Bharg Technologies GmbH. We keep your information confidential but may share it with our shareholders, affiliates, and business partners. Whenever we transfer your personal data outside of the European Economic Area (EEA) we ensure that we have standard contractual clauses in place as a safeguard for this transaction.</p>
                <p>We may also disclose it to our personnel, suppliers or subcontractors insofar as it is reasonably necessary for the purposes set out in this privacy policy, provided that they do not make independent use of the information and have agreed to adhere to the rules set out in this privacy statement.</p>
                <p>In addition, we may disclose your information to the extent that we are required to do so by law (which may include to government bodies and law enforcement agencies); in connection with any legal proceedings or prospective legal proceedings; and to establish, exercise or defend our legal rights (including providing information to others for the purposes of fraud prevention).</p>
                <p>If we are involved in a merger, acquisition, or sale of all or a portion of our business or assets, the information we hold may be included as part of that sale, in which case you may be notified, as appropriate, via email and/or a notice on the website of any changes in ownership or use of your information, as any choices you may have regarding that information.</p>
                <p>The transfer to the subcontractors' subcontractors is based on your consent.</p>
                <p>Except as provided above, we will not provide your information to third parties.</p>

                <p style={text}>3. USE OF SUB-PROCESSORS</p>
                <p>Our website is hosted by Digital Ocean and Microsoft github. Although the information is stored on servers located in the EU, they are also considered to have been exported to countries outside the EU, as the two companies are located in the United States and otherwise use subcontractors in other countries outside the EU. An overview of Digital Ocean's subcontractors can be found in Digital Ocean website. An overview of the subcontractors used by Github can be found at <a rel="noopener noreferrer" href="https://github.com" target="_blank">www.github.com</a> and thus the countries to which data must be considered exported can be found here. Our transfer of data to the two subcontractors is based either on the EU Commission's Standard Contracts or on the EU / US Privacy Shield.</p>

                <p style={text}>4. YOUR RIGHTS IN RELATION TO PERSONAL DATA WHICH WE PROCESS RELATING TO YOU</p>
                <p>You have the following rights over the way we process personal data relating to you:</p>
                <ul style={{marginTop: '10px', marginLeft: "20px"}}>
                    <li>You can ask for a copy of the data we are processing about you, and you can have inaccuracies corrected;</li>
                    <li>You can ask us to restrict, stop processing, or to delete your personal data;</li>
                    <li>You can request a machine-readable copy of your personal data, which you can use with another service provider. Where it is technically feasible, you can ask us to send this information directly to another provider if you prefer;</li>
                    <li>You can make a complaint to a data protection regulator.</li>
                </ul>
                <p>We aim to comply without undue delay and within one month at the latest. Please send your requests to <a rel="noopener noreferrer" href={WEBSITE} target="_blank">www.bharg.com</a>.</p>

                <p style={text}>5. CHILDREN</p>
                <p>We do not use the website to knowingly solicit data from or market to children under the age of 13. If a parent or guardian becomes aware that his or her child has provided us with information without their consent, he or she should contact us at <a href={`mailto:${EMAIL}`}>hello@bharg.com</a>. We will delete such information within a reasonable time.</p>

                <p style={text}>6. SECURITY</p>
                <p>We have implemented security measures to ensure that our internal procedures meet our high security policy standards. Accordingly, we strive to protect the quality and integrity of your information.</p>

                <p style={text}>7. CHANGES</p>
                <p>This policy may be updated from time to time. We will notify you of any changes by posting the new policy here and, where feasible, by letting you know by email.</p>

                <p style={text}>8. INTERNATIONAL DATA TRANSFERS</p>
                <p>As mentioned above, where we transfer your data outside of the EEA, we have agreements in place with those parties, which include standard data protection clauses adopted by a data protection regulator and approved by the European Commission to ensure that appropriate safeguards are in place to protect your personal data. If you would like to find out more about these safeguards, please let us know by writing to <a href={`mailto:${EMAIL}`}>hello@bharg.com</a>.</p>

                <p style={text}>9. LINKS TO OTHER WEBSITES</p>
                <p>Our website may contain links to other websites or to integrated sites. We are not responsible for the contents of other companies' websites or for the practices of such companies regarding the collection of personal data. When you visit other websites, you should read the owners' policies on the protection of personal data and other relevant policies.</p>

                <p style={text}>10. RETENTION PERIODS</p>
                <p>We will hold your personal information on our systems for as long as is necessary for the relevant purpose, or as otherwise described in this privacy policy. In general, we store your data for 5 years as of the most recent update in our systems. If you have any questions or comments about this privacy policy, please let us know by email to <a href={`mailto:${EMAIL}`}>hello@bharg.com</a>.</p>
            </div>
        </div>
    )
}

const text = {
    marginTop: "20px",
    textTransform: "uppercase"
}; 

export default PrivacyPolicy;
