import React, { useState } from 'react'
// import SectionHead from '../layouts/SectionHead'
import PrivacyPolicy from '../layouts/PrivacyPolicy'
import CookiePolicy from '../layouts/CookiePolicy'
import CompanyDetails from '../layouts/CompanyDetails'

const LegalLinks = () => {

    const [current, setCurrent] = useState(0)

    return (
        <div className='main-content-container'>
            <div className='legal-links-con'>
                <h1
                    style={{ padding: '4rem 0 45px' }}
                >LEGAL LINKS</h1>
                <div className='button_con'>
                    <button
                        className={current === 0 ? 'current' : null}
                        onClick={() => setCurrent(0)}
                    >Privacy Policy</button>
                    <button
                        className={current === 1 ? 'current' : null}
                        onClick={() => setCurrent(1)}
                    >Cookies</button>
                    <button
                        className={current === 2 ? 'current' : null}
                        onClick={() => setCurrent(2)}
                    >Company Details</button>
                </div>

                <div style={{ margin: '40px 0' }}>
                    {current === 0 && <PrivacyPolicy />}
                    {current === 1 && <CookiePolicy />}
                    {current === 2 && <CompanyDetails />}
                </div>

            </div>
        </div>
    )
}

export default LegalLinks
