import React from 'react';
import { WEBSITE } from '../../utils/exportContent';

function CookiePolicy() {
    return (
        <div style={{paddingBottom: "50px"}}>
            <h1 style={{marginBottom: "40px"}}>ALL ABOUT OUR COOKIES</h1>
            <p>Cookies are small text files that can be used by websites to make a user's experience more efficient.</p>
            <p>We use cookies to personalise content and ads, to provide social media features and to analyse our traffic. We also share information about your use of our site with our social media, advertising and analytics partners who may combine it with other information that you‚Äôve provided to them or that they‚Äôve collected from your use of their services.</p>
            <p>This site uses different types of cookies. Some cookies are placed by third party services that appear on our pages.</p>
            <p>Learn more about who we are, how you can contact us and how we process personal data in our Privacy Policy.</p>
            
            <p style={text}>Necessary Necessary cookies help make a website usable by enabling basic functions like page navigation and access to secure areas of the website. The website cannot function properly without these cookies</p>
            
            <p style={text}>Preferences Preference cookies enable a website to remember information that changes the way the website behaves or looks, like your preferred language or the region that you are in: - lang, <a rel="noopener noreferrer" href="https://ads.linkedin.com" target="_blank">ads.linkedin.com</a>: Remembers the user's selected language version of a website - lang, <a rel="noopener noreferrer" href="https://linkedin.com" target="_blank">linkedin.com</a></p>


            <p style={text}>Statistics Statistic cookies help us understand how visitors interact with websites by collecting and reporting information anonymously:</p>
            <p>- ga, <a rel="noopener noreferrer" href={WEBSITE} target="_blank">www.bharg.com</a>: Registers a unique ID that is used to generate statistical data on how the visitor uses the website. - gat, <a rel="noopener noreferrer" href={WEBSITE} target="_blank">www.bharg.com</a>: Used by Google Analytics to throttle request rate - gid, <a rel="noopener noreferrer" href={WEBSITE} target="_blank">www.bharg.com</a>: Registers a unique ID that is used to generate statistical data on how the visitor uses the website. - aka_debug, <a rel="noopener noreferrer" href="https://akamaized.net" target="_blank">akamaized.net</a>: Used by Vimeo to track usage of their embedded video player.</p>

            <p style={text}>Marketing Marketing cookies are used to track visitors across websites. The intention is to display ads that are relevant and engaging for the individual user and thereby more valuable for publishers and third party advertisers:- fbp, <a rel="noopener noreferrer" href={WEBSITE} target="_blank">www.bharg.com</a>: Used by Facebook to deliver a series of advertisement products such as real time bidding from third party advertisers. - bcookie, <a rel="noopener noreferrer" href="https://linkedin.com/" target="_blank">www.linkedin.com</a>: Used by the social networking service, LinkedIn, for tracking the use of embedded services. - bscookie, <a rel="noopener noreferrer" href="https://linkedin.com/" target="_blank">www.linkedin.com</a>: Used by the social networking service, LinkedIn, for tracking the use of embedded services. - fr, <a rel="noopener noreferrer" href="https://facebook.com/" target="_blank">www.facebook.com</a>: Used by Facebook to deliver a series of advertisement products such as real time bidding from third party advertisers. - lidc, <a rel="noopener noreferrer" href="https://linkedin.com/" target="_blank">www.linkedin.com</a>: Used by the social networking service, LinkedIn, for tracking the use of embedded services. - lissc, <a rel="noopener noreferrer" href="https://linkedin.com/" target="_blank">www.linkedin.com</a>: Used by the social networking service, LinkedIn, for tracking the use of embedded services. - text_cookie, <a rel="noopener noreferrer" href="https://marketingplatform.google.com/about/enterprise/" target="_blank">doubleclick.net</a>: Used to check if the user's browser supports cookies. - UserMatchHistory, <a rel="noopener noreferrer" href="https://linkedin.com/" target="_blank">www.linkedin.com</a>: Used to track visitors on multiple websites, in order to present relevant advertisement based on the visitor's.</p>
        </div>
    )
}

const text = {
    marginTop: "20px"
}; 

export default CookiePolicy;
